import { XRAnalytics } from '../types';

type paramValueType = string | number | boolean | null | undefined | { toJSON: () => string };

export default class MetalitixLoggerEvent {
  private object = 'user.event';
  private eventName: string;
  private eventType: XRAnalytics.UserInteractionTypes | string;
  private target?: string | XRAnalytics.EventPoint | any;
  private points?: XRAnalytics.EventPoint[];
  private params: { [key: string]: paramValueType } = {};

  constructor(
    eventName: string,
    eventType: XRAnalytics.UserInteractionTypes | string,
    target?: string | XRAnalytics.EventPoint | any,
    points?: XRAnalytics.EventPoint[],
  ) {
    this.eventName = eventName;
    this.eventType = eventType;
    this.target = target;
    this.points = points;
  }

  public setCustomEventData = (paramKey: string, paramValue: paramValueType) => {
    this.params[paramKey] = paramValue;
  };

  public toJSON = (): XRAnalytics.UserEvent => {
    return {
      object: this.object,
      eventName: this.eventName,
      eventType: this.eventType,
      target: this.target,
      points: this.points,
      params: this.params,
    };
  };
}
